.login{
    width: 480px;
    height: 100vh;
    float: right;
    position: relative;

    display: flex;
    align-items: center;
    justify-content: center;

    &__holderbtn{
        position: absolute;
        top: 32px;
        right: 32px;
        &-button{
            width: 121px;
            height: 40px;
        }
    }

    &__window{
        width: 310px;
        &-text{
            width: 100%;
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 500;
            font-size: 12px;
            line-height: 16px;
            margin-top: 16px;
            text-align: center;
            &-link{
                text-decoration: none;
                color: #4E94CA;
            }
        }
        &-title{
            width: 100%;

            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 24px;
            text-align: center;

            color: #0B0E2D;
            margin-bottom: 24px;
        }
        &-input-holder{
            width: 100%;

            margin-bottom: 16px;
            position: relative;
        }
        &-login, &-password{
            width: calc(100% - 32px);
            height: 48px;


            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 500;
            font-size: 13px;
            line-height: 20px;

            display: flex;
            align-items: center;

            color: #0B0E2D;
        }
        &-password{
            font-family: 'Ubuntu';
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 21px;
        }
        &-password-eye{
            width: 23px;
            height: 23px;
            margin: 12px;
            position: absolute;
            cursor: pointer;
            display: block;
            background: url(/images/icons/eyesOff.svg);
            background-color: #F0F2F7;
            right: 0;
            top: 0;
        }
        &-link-holder{
            margin-bottom: 16px;
        }
        &-link{
            font-size: 13px;
            color: #4E94CA;
            text-decoration: none;
        }
        &-holderbtn{
            width: 100%;
        }
        &-btn{
            width: 100%;
            height: 40px;
        }
    }
    &__footer{
        width: 100%;
        padding: 24px 0;
        position: absolute;
        display: flex;
        justify-content: space-around;
        align-items: center;
        bottom: 0;
        left: 0;


        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 16px;
        &-right, &-link{
            display: flex;
            align-items: center;
            color: rgba(11, 15, 50, 0.5);
        }
        &-link{
            color: #4E94CA;
            opacity: 0.5;
            text-decoration: none;
        }
    }
}